@charset "UTF-8";
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/* ================================================================ Reset CSS */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: inherit;
  font-style: inherit;
  font-size: 100%;
  font-family: inherit;
  vertical-align: baseline;
}
html:not(.focus-visible),
body:not(.focus-visible),
div:not(.focus-visible),
span:not(.focus-visible),
applet:not(.focus-visible),
object:not(.focus-visible),
iframe:not(.focus-visible),
h1:not(.focus-visible),
h2:not(.focus-visible),
h3:not(.focus-visible),
h4:not(.focus-visible),
h5:not(.focus-visible),
h6:not(.focus-visible),
p:not(.focus-visible),
blockquote:not(.focus-visible),
pre:not(.focus-visible),
a:not(.focus-visible),
abbr:not(.focus-visible),
acronym:not(.focus-visible),
address:not(.focus-visible),
big:not(.focus-visible),
cite:not(.focus-visible),
code:not(.focus-visible),
del:not(.focus-visible),
dfn:not(.focus-visible),
em:not(.focus-visible),
font:not(.focus-visible),
img:not(.focus-visible),
ins:not(.focus-visible),
kbd:not(.focus-visible),
q:not(.focus-visible),
s:not(.focus-visible),
samp:not(.focus-visible),
small:not(.focus-visible),
strike:not(.focus-visible),
sub:not(.focus-visible),
sup:not(.focus-visible),
tt:not(.focus-visible),
var:not(.focus-visible),
dl:not(.focus-visible),
dt:not(.focus-visible),
dd:not(.focus-visible),
ol:not(.focus-visible),
ul:not(.focus-visible),
li:not(.focus-visible),
fieldset:not(.focus-visible),
form:not(.focus-visible),
label:not(.focus-visible),
legend:not(.focus-visible),
table:not(.focus-visible),
caption:not(.focus-visible),
tbody:not(.focus-visible),
tfoot:not(.focus-visible),
thead:not(.focus-visible),
tr:not(.focus-visible),
th:not(.focus-visible),
td:not(.focus-visible) {
  outline: 0;
}

/* remember to define focus styles! */
:focus {
  outline-style: auto;
}

body {
  line-height: 1;
  color: #000;
  background: #fff;
}

ol,
ul {
  list-style: none;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: separate;
  border-spacing: 0;
}

/*caption, th, td {
   text-align: left;
   font-weight: normal;
}*/
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
}

blockquote,
q {
  quotes: "" "";
}

textarea,
input[type=text] {
  box-sizing: border-box;
}

.textarea-autosize {
  transition: height 0.1s ease;
  resize: none !important;
}

/* Elements that are disabled, make sure they reflect that with UX feedback. */
[disabled] {
  opacity: 0.5;
  pointer-events: none;
}

/* ===================================================================== Grid */
.Row {
  margin: auto;
  width: 960px;
}

.ContentColumn {
  margin: 0 0 0 230px;
}

/* ==================== Pages that hide the panel and have full-width content */
body.NoPanel #Panel,
body.Entry #Panel,
body.Conversations.add #Panel,
body.Vanilla.Post #Panel {
  display: none;
}

body.NoPanel #Content,
body.Conversations.add #Content,
body.Vanilla.Post #Content {
  width: auto;
  margin: 0;
}

body.NoPanel #Content {
  margin: auto;
}

body.NarrowForm #Content {
  max-width: 700px;
}

body.Entry #Content {
  float: none;
  margin: 0 auto;
  max-width: 600px;
}

/* ============================================== General Styles & Typography */
body {
  color: #000;
  font-family: "lucida grande", "Lucida Sans Unicode", tahoma, sans-serif;
  font-size: 75%;
  font-size: small;
  line-height: 1.7em;
  background: #fff;
  margin: 0;
  padding: 0;
}

#Body {
  zoom: 1;
  /* hasLayout for IE6/7 */
}

#Body:after {
  /* clearfix */
  visibility: hidden;
  display: block;
  content: "";
  clear: both;
  height: 0;
}

.Invisible {
  opacity: 0;
}

.ClearFix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.ClearFix {
  display: inline-table;
}

/* Hides from IE-mac \*/
* html .ClearFix {
  height: 1%;
}

.ClearFix {
  display: block;
}

/* End hide from IE-mac */
.nowrap {
  white-space: nowrap;
}

.Center {
  text-align: center;
}

.Right {
  text-align: right;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Helvetica Neue", Helvetica, arial, sans-serif;
  font-weight: bold;
  margin: 5px 0;
}

p {
  margin: 5px 0;
}

.P {
  margin: 5px 0;
}

h1 {
  font-size: 140%;
}

h2 {
  font-size: 120%;
}

h3 {
  font-size: 110%;
}

h4 {
  font-size: 110%;
}

h5 {
  font-size: 100%;
}

a {
  text-decoration: none;
  color: #1e79a7;
}

a:hover,
a.TextColor:hover,
a:hover .TextColor {
  color: #ff0084;
}

a.TextColor,
a .TextColor {
  color: #000;
}

img.Thumbnail {
  max-height: 300px;
  max-width: 100px;
  float: left;
  margin: 0 16px 0 0;
}

input.DateBox,
input.InputBox,
input.SmallInput,
textarea {
  font-family: "lucida grande", "Lucida Sans Unicode", tahoma, sans-serif;
  color: #333;
  font-size: 15px;
  padding: 3px;
  margin: 0;
  width: 250px;
  background: #fff;
  border: 1px solid #999;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.PasswordStrength {
  width: 250px;
}

.ui-datepicker {
  width: 17em;
  padding: 2px 20px 2px 10px;
  display: none;
  background: #e6e6e6;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #999;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.ui-datepicker .ui-datepicker-header {
  position: relative;
  padding: 0.2em 0;
}

.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
  position: absolute;
  top: 2px;
  width: 1.8em;
  height: 1.8em;
}

.ui-datepicker .ui-datepicker-prev-hover,
.ui-datepicker .ui-datepicker-next-hover {
  top: 1px;
}

.ui-datepicker .ui-datepicker-prev {
  left: 2px;
}

.ui-datepicker .ui-datepicker-next {
  right: 2px;
}

.ui-datepicker .ui-datepicker-prev-hover {
  left: 1px;
}

.ui-datepicker .ui-datepicker-next-hover {
  right: 1px;
}

.ui-datepicker .ui-datepicker-prev span,
.ui-datepicker .ui-datepicker-next span {
  display: block;
  position: absolute;
  left: 50%;
  margin-left: -8px;
  top: 50%;
  margin-top: -8px;
}

.ui-datepicker .ui-datepicker-title {
  margin: 0 2.3em;
  line-height: 1.8em;
  text-align: center;
}

.ui-datepicker .ui-datepicker-title select {
  font-size: 1em;
  margin: 1px 0;
}

.ui-datepicker select.ui-datepicker-month-year {
  width: 100%;
}

.ui-datepicker select.ui-datepicker-month,
.ui-datepicker select.ui-datepicker-year {
  width: 49%;
}

.ui-datepicker table {
  width: 100%;
  font-size: 0.9em;
  border-collapse: collapse;
  margin: 0 0 0.4em;
}

.ui-datepicker th {
  padding: 0.7em 0.3em;
  text-align: center;
  font-weight: bold;
  border: 0;
}

.ui-datepicker td {
  border: 0;
  padding: 1px;
}

.ui-datepicker td span,
.ui-datepicker td a {
  display: block;
  padding: 0.2em;
  text-align: right;
  text-decoration: none;
}

.ui-datepicker .ui-datepicker-buttonpane {
  background-image: none;
  margin: 0.7em 0 0 0;
  padding: 0 0.2em;
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}

.ui-datepicker .ui-datepicker-buttonpane button {
  float: right;
  margin: 0.5em 0.2em 0.4em;
  cursor: pointer;
  padding: 0.2em 0.6em 0.3em 0.6em;
  width: auto;
  overflow: visible;
}

.ui-datepicker .ui-datepicker-buttonpane button.ui-datepicker-current {
  float: left;
}

/* with multiple calendars */
.ui-datepicker.ui-datepicker-multi {
  width: auto;
}

.ui-datepicker-multi .ui-datepicker-group {
  float: left;
}

.ui-datepicker-multi .ui-datepicker-group table {
  width: 95%;
  margin: 0 auto 0.4em;
}

.ui-datepicker-multi-2 .ui-datepicker-group {
  width: 50%;
}

.ui-datepicker-multi-3 .ui-datepicker-group {
  width: 33.3%;
}

.ui-datepicker-multi-4 .ui-datepicker-group {
  width: 25%;
}

.ui-datepicker-multi .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-multi .ui-datepicker-group-middle .ui-datepicker-header {
  border-left-width: 0;
}

.ui-datepicker-multi .ui-datepicker-buttonpane {
  clear: left;
}

.ui-datepicker-row-break {
  clear: both;
  width: 100%;
  font-size: 0;
}

/* RTL support */
.ui-datepicker-rtl {
  direction: rtl;
}

.ui-datepicker-rtl .ui-datepicker-prev {
  right: 2px;
  left: auto;
}

.ui-datepicker-rtl .ui-datepicker-next {
  left: 2px;
  right: auto;
}

.ui-datepicker-rtl .ui-datepicker-prev:hover {
  right: 1px;
  left: auto;
}

.ui-datepicker-rtl .ui-datepicker-next:hover {
  left: 1px;
  right: auto;
}

.ui-datepicker-rtl .ui-datepicker-buttonpane {
  clear: right;
}

.ui-datepicker-rtl .ui-datepicker-buttonpane button {
  float: left;
}

.ui-datepicker-rtl .ui-datepicker-buttonpane button.ui-datepicker-current,
.ui-datepicker-rtl .ui-datepicker-group {
  float: right;
}

.ui-datepicker-rtl .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-rtl .ui-datepicker-group-middle .ui-datepicker-header {
  border-right-width: 0;
  border-left-width: 1px;
}

textarea.TextBox {
  width: 500px;
  height: 100px;
  min-height: 100px;
}

.TextBoxWrapper {
