  font-size: 14px;
  color: #222;
  margin: 0;
  padding: 3px;
}

.Button {
  display: inline-block;
  cursor: pointer;
  margin: 0;
  font-size: 12px;
  line-height: 1;
  font-weight: bold;
  padding: 4px 6px;
  background: #f8f8f8;
