  border-right-width: 1px;
}

body:not(.dataDriven) .Button.Active,
body:not(.dataDriven) .ButtonGroup.Open .Button.Handle,
body:not(.dataDriven) .Button:focus,
body:not(.dataDriven) .Button:active {
