  display: inline-block;
  cursor: pointer;
  font-size: 13px;
  line-height: 16px;
  font-weight: bold;
  padding: 5px 8px;
  border: 1px solid #999;
  border-radius: 2px;
  white-space: nowrap;
  background-color: #fdfdfd;
}

body:not(.dataDriven) .ButtonGroup.Open .NavButton.Handle {
