}

.DismissMessage {
  font-size: 13px;
  text-align: left;
  position: relative;
  color: #000;
  border: none;
  border-radius: 1px;
  margin: 10px 0;
  padding: 10px;
}

.DismissMessage img {
  max-width: 100%;
}

.DismissMessage.Info {
  background: #f3f4f8;
  border: 1px solid #ddd;
}

.DismissMessage.Warning {
  background: #ffebe9;
  border: 1px solid #ffccc9;
}

.DismissMessage.Box {
  background: #fff8ce;
  border: 1px solid #c5bea4;
  box-shadow: none;
  margin: 0 0 10px;
  /* Make sure that .Box definitions don't override the base DismissMessage margin! */
}

.CasualMessage {
  background: #cfecff;
  border: 1px solid #abdafb;
  color: #1e79a7;
}

.InfoMessage {
  background: #f6f6f6;
  border: 1px solid #ddd;
}

.AlertMessage {
  background: #fff8ce;
  border: 1px solid #deddaf;
}

.WarningMessage {
  background: #ffebe9;
  border: 1px solid #ffccc9;
}

