.item-attachment .dl-columns {
  overflow: hidden;
  margin-right: -10px;
  margin-left: -10px;
}

.item-attachment .dl-columns:before,
.item-attachment .dl-columns:after {
  display: table;
  content: " ";
}

.item-attachment .dl-columns:after {
  clear: both;
}

.item-attachment .dl-columns dt {
  width: 20%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.item-attachment .dl-columns dd {
  width: 30%;
}

.item-attachment .dl-columns dt,
.item-attachment .dl-columns dd {
  float: left;
  padding: 0 10px;
  box-sizing: border-box;
}

