## Alerts

.alert-info    - Informative alert
.alert-warning - Warning alert
.alert-danger  - Danger alert

Styleguide Alerts
*/
.item-attachment .alert {
  padding: 12px;
  margin-bottom: 20px;
  color: #222;
  background-color: #d8ecfc;
  border-color: #d8ecfc;
  border-style: solid;
  border-width: 1px;
  border-radius: 2px;
}

.item-attachment .alert a {
  color: #090909;
}

.item-attachment .alert hr {
  border-top-color: #c0e0fa;
}

.item-attachment .alert h4 {
  margin-top: 0;
  color: inherit;
}

.item-attachment .alert p,
.item-attachment .alert ul {
  margin-bottom: 0;
}

.item-attachment .alert p + p {
  margin-top: 5px;
}

/* Informative alert */
.item-attachment .alert-info {
  color: #222;
  background-color: #f9edbe;
  border-color: #f9edbe;
}

.item-attachment .alert-info a {
  color: #090909;
}

.item-attachment .alert-info hr {
  border-top-color: #f7e7a7;
}

/* Warning alert */
.item-attachment .alert-warning {
  color: #222;
  background-color: #ffcc00;
  border-color: #ffcc00;
}

.item-attachment .alert-warning a {
  color: #090909;
}

.item-attachment .alert-warning hr {
  border-top-color: #e6b800;
}

/* Danger alert */
.item-attachment .alert-danger {
  color: #fff;
  background-color: #b90000;
  border-color: #b90000;
}

.item-attachment .alert-danger a {
  color: #e6e6e6;
}

.item-attachment .alert-danger hr {
  border-top-color: #a00;
}

/*
## Base item

Styleguide Item
*/
.item-attachment .item-heading {
  margin-bottom: 0 !important;
  /* Hack */
  font-weight: 600;
}

.item-attachment .item-body {
  overflow: hidden;
}

.item-attachment .item-body:before,
.item-attachment .item-body:after {
  display: table;
  content: " ";
}

.item-attachment .item-body:after {
  clear: both;
}

.item-attachment .item-body > *:first-child {
  margin-top: 0;
}

.item-attachment .item-body > *:last-child {
  margin-bottom: 0;
}

.item-attachment .item-header + .item-body {
  margin-top: 5px;
}

.item-attachment .item-meta {
  font-size: 12px;
  line-height: 20px;
  color: #999;
}

.item-attachment .item-meta > span {
  padding-right: 5px;
  padding-left: 5px;
}

.item-attachment .item-meta > span:first-child {
  padding-left: 0;
}

.item-attachment .item-meta > span:last-child {
  padding-right: 0;
}

/*
## Item attachments

Styleguide Attachments
*/
.item-attachment {
  margin-top: 20px;
}

.item-attachment .alert {
  margin-bottom: 0;
}

.item-attachment .media-object .icon {
  display: block;
  min-width: 32px;
  font-size: 32px;
  text-align: center;
  line-height: 1;
}

/**
 * Twitter card embed.
 */
.twitter-card-preload {
  opacity: 0;
}

.twitter-card-loaded {
  transition: all 0.2s ease;
  opacity: 1;
}

/* Effects */
@keyframes highlight {
  0% {
    background-color: #ff9;
  }
  10% {
    background-color: #ff9;
  }
}
.highlight-effect {
  animation: highlight 0.8s;
}

/* Groups section tweaks */
.Groups.Section-DiscussionList .Group-Header,
.Section-Group.Section-Discussion .Group-Header {
  margin-bottom: 0;
}

.Groups.Section-DiscussionList #Panel,
.Section-Group.Section-Discussion #Panel {
  float: none;
  position: absolute;
}

.Groups.Section-DiscussionList .HomepageTitle {
  display: none;
}

.InputBox {
  background: #fff;
  border: 1px solid #999;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.jcrop-holder {
  direction: ltr;
  text-align: left;
}

.jcrop-vline,
.jcrop-hline {
  background: #ffffff url("images/jcrop.gif");
  font-size: 0;
  position: absolute;
}

.jcrop-vline {
  height: 100%;
  width: 1px !important;
}

.jcrop-vline.right {
  right: 0;
}

.jcrop-hline {
  height: 1px !important;
  width: 100%;
}

.jcrop-hline.bottom {
  bottom: 0;
}

.jcrop-tracker {
  height: 100%;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  user-select: none;
  -webkit-user-select: none;
}

.jcrop-handle {
  background-color: #333333;
  border: 1px #eeeeee solid;
  width: 7px;
  height: 7px;
  font-size: 1px;
}

.jcrop-handle.ord-n {
  left: 50%;
  margin-left: -4px;
  margin-top: -4px;
  top: 0;
}

.jcrop-handle.ord-s {
  bottom: 0;
  left: 50%;
  margin-bottom: -4px;
  margin-left: -4px;
}

.jcrop-handle.ord-e {
  margin-right: -4px;
  margin-top: -4px;
  right: 0;
  top: 50%;
}

.jcrop-handle.ord-w {
  left: 0;
  margin-left: -4px;
  margin-top: -4px;
  top: 50%;
}

.jcrop-handle.ord-nw {
  left: 0;
  margin-left: -4px;
  margin-top: -4px;
  top: 0;
}

.jcrop-handle.ord-ne {
  margin-right: -4px;
  margin-top: -4px;
  right: 0;
  top: 0;
}

.jcrop-handle.ord-se {
  bottom: 0;
  margin-bottom: -4px;
  margin-right: -4px;
  right: 0;
}

.jcrop-handle.ord-sw {
  bottom: 0;
  left: 0;
  margin-bottom: -4px;
  margin-left: -4px;
}

.jcrop-dragbar.ord-n,
.jcrop-dragbar.ord-s {
  height: 7px;
  width: 100%;
}

.jcrop-dragbar.ord-e,
.jcrop-dragbar.ord-w {
  height: 100%;
  width: 7px;
}

.jcrop-dragbar.ord-n {
  margin-top: -4px;
}

.jcrop-dragbar.ord-s {
  bottom: 0;
  margin-bottom: -4px;
}

.jcrop-dragbar.ord-e {
  margin-right: -4px;
  right: 0;
}

.jcrop-dragbar.ord-w {
  margin-left: -4px;
}

.jcrop-light .jcrop-vline,
.jcrop-light .jcrop-hline {
  background: #ffffff;
  filter: alpha(opacity=70) !important;
  opacity: 0.7 !important;
}

.jcrop-light .jcrop-handle {
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  background-color: #000000;
  border-color: #ffffff;
  border-radius: 3px;
}

.jcrop-dark .jcrop-vline,
.jcrop-dark .jcrop-hline {
  background: #000000;
  filter: alpha(opacity=70) !important;
  opacity: 0.7 !important;
}

.jcrop-dark .jcrop-handle {
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  background-color: #ffffff;
  border-color: #000000;
  border-radius: 3px;
}

.solid-line .jcrop-vline,
.solid-line .jcrop-hline {
  background: #ffffff;
}

.jcrop-holder img,
img.jcrop-preview {
  max-width: none;
}

.padded {
  margin-top: 18px;
  margin-bottom: 18px;
}

.padded-top {
  margin-top: 18px;
}

.padded-bottom {
  margin-bottom: 18px;
}

.padded-left {
  margin-left: 18px;
}

.padded-right {
  margin-right: 18px;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flex > * {
  margin-right: 4.5px;
}
.flex > *:last-child {
  margin-right: 0;
}

.flex-wrap {
  flex-wrap: wrap;
}

.shrink {
  flex-shrink: 1;
}

.grow {
  flex-grow: 1;
}

.no-shrink {
  flex-shrink: 0;
}

.no-grow {
  flex-grow: 0;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.Hidden,
.hidden {
  display: none;
}

.progress {
  background: url("images/progress.gif") center center no-repeat;
}

.foggy {
  position: relative;
  -webkit-filter: blur(2px);
}
.foggy input {
  user-select: none;
}
.foggy::before {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-color: #fff;
  z-index: 1;
  content: "";
}